/* Footer CSS */
.footer-sns-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: fit-content;
    margin: 60px 0 40px 0;

    
}

.footer-insta-icon {
    width: 12px;
    height: 12px;
    margin-right: 8px;
}

.footer-insta-id {
    font-family: 'Gadugi';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    /* identical to box height, or 133% */

    letter-spacing: 0.04em;
    text-decoration: none;;

    color: #FFFFFF;
}

.copyright {
    margin-bottom: 40px;
    font-family: 'Yoon YMjO 540_TT';
    font-style: normal;
    font-weight: 400;
    font-size: 8px;
    line-height: 12px;
    /* identical to box height, or 150% */

    text-align: center;
    letter-spacing: 0.25px;

    /* white */

    color: #666666;
}