* {
    background-color: #1C1E1E;
    max-width: 900px;
}

p {
    margin: 0px;
}


/* Header CSS */
.roughin-logo-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.img-div {
    position: relative;
    margin-top: 32px;
    display: flex;
    justify-content: center;
}

.roughin-logo {
    position: relative;
    width: 50px;
    height: 75px;
    
}

.nav {
    position: relative;
    margin-top: 32px;
    display: flex;
    justify-content: center;
    text-align: center;


    font-family: 'Yoon YMjO 540_TT';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: 0.25px;
    

}

.nav-tab-active{
    position: relative;
    margin-right: 16px;;
    color: #FFFFFF;
    text-decoration: none;

}

.nav-tab-inactive{
    position: relative;
    margin-right: 16px;;
    color: #666666;
    text-decoration: none;
}


/* Body CSS */
.menu-detail-subContainer {
    margin: 40px 1em 0px 1em;
    display: flex;
    justify-content: center;
}

.menu-detail {
    position: relative;
    flex-direction: column;
    margin: 0 0.5em 0 0.5em;
    width: 170px;
    height: fit-content;
}

.menu-detail-title {
    width: 170px;
    margin-top: 4px;
    font-family: 'Yoon YMjO 550_TT';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    letter-spacing: -0.25px;
    color: white;
}

.menu-detail-price {
    width: 170px;
    margin-top: 4px;
    font-family: 'Yoon YMjO 540_TT';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    letter-spacing: 0.25px;
    color: #FFFFFF;
    
}

.menu-detail-desc {
    width: 170px;
    margin-top: 4px;
    font-family: 'Yoon YMjO 540_TT';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    letter-spacing: 0.25px;
    color: #FFFFFF;
    white-space: pre-line;
}