@font-face {
    /* 사용할 폰트의 이름 */
    font-family: "Gadugi";
    font-display: fallback;

    /* src: 사용할 폰트의 위치 및 형식 */

    src: 
    url("./Gadugi/Gadugi.ttf") format("truetype"),
    url("./Gadugi/Gadugi.woff2") format("woff2"),
    url("./Gadugi/Gadugi.woff") format("woff"), 
    url("./Gadugi/Gadugi.eot?iefix") format("embedded-opentype");


    /* 사용할 폰트의 기본 스타일 */
    font-weight: 400;
    font-style: normal;
}

